@use 'variables' as v;
@use 'mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    overflow-x: hidden;
    
    @include m.tablet{
        height: 100%;
    }

    &.no-scroll{
        overflow-y: hidden;
    }
}
*, *:before, *:after {
    box-sizing: inherit;
}   

.container {
    width: 80%;
    max-width: 1200px;
    margin: 0 auto;
    
    @include m.telefono {
      width: 90%;
    }
}

.container-left {
    width: 80%;
    max-width: 120rem;
    margin: 0 auto 0 10rem;
    
    @include m.telefono {
      width: 90%;
    }
}


a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

.swal2-popup {
    font-size: 1.6rem !important; /* Tamaño de fuente */
    width: 32em !important; /* Ancho */
}